  <!-- Abstract -->
  <div id="abstract" class="container mx-auto px-4 pb-16 pt-24">
    <div class="items-center flex flex-wrap">
      <div class="w-full ml-auto px-12 md:px-4">
        <div class="md:pr-12">
          <div>
            <div
              class="text-blueGray-500 p-3 text-center inline-flex items-center justify-center w-16 h-16 mb-6 shadow-lg rounded-full bg-white"
            >
              <i class="fas fa-file-alt text-xl" />
            </div>
            <h3 class="text-3xl font-semibold">Abstract</h3>
            <p class="mt-4 text-lg leading-relaxed text-blueGray-500 text-justify">
              Visual Inertial Odometry (VIO) is the task of finding the accurate camera trajectory
              using camera and inertial measurement unit (IMU) sensors. There are a large variety of
              applications for the VIO, such as augmented reality and indoor navigation. Using VIO
              methods, a visually impaired person might be able to navigate inside indoor
              environments and outdoor. However, dynamic environments such as a crowded corridor are
              still exceedingly challenging for popular visual-inertial odometry VIO algorithms.
              Existing VIO datasets, e.g. ADVIO, typically fail to effectively exploit challenges
              and capture their significance. To address this issue and improve visually impaired
              people's navigation systems, we introduce the AUT-VI dataset, a novel, generous and
              super challenging dataset with 130 diverse sequences in 17 different locations for
              evaluating VI/VIO algorithms in the context of dynamic objects, robust
              loop-closure/map-reuse, lighting reflection and sharp movements to cover all extreme
              navigation scenarios. This benchmark provides camera images with 1024x280 resolution
              at 30 Hz and, accelerations and angular velocities from IMU on 3 axes at 200Hz
              recorded with a smartphone using our unique application. For trajectory evaluation, we
              also provide accurate pose ground truth from GPS and IMU outdoors and motion capture
              in indoors. Furthermore, to make our contribution complete and provide the necessary
              tools for further development, we make the android application publicly available so
              that fellow researchers can easily record their own variation of VIO dataset, an easy
              solution to a previously tiresome problem (dataset recording). Also, The full dataset
              is publicly available. We also evaluate state-of-the-art VIO, VO and dynamic SLAM
              approaches on our dataset to show the significance of the need for this dataset.
            </p>
          </div>
        </div>
      </div>
    </div>

